(function() {
  'use strict';

  angular
    .module('frontend', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngOrderObjectBy', 'ui.router', 'ngMaterial', 'toastr']);

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .directive('mySidenav', MySidenav);

  /** @ngInject */
  function MySidenav() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/sidenav/sidenav.html',
      controller: SidenavController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function SidenavController($state, device,auth, $rootScope) {
      var vm = this;
      vm.gotoDevice = gotoDevice;
      vm.devices = [];
      vm.isOpen = true;
      vm.open = open;
      vm.update = update;
      $rootScope.sidenav = vm;

      update();

      function update() {
        if(auth.status.loggedIn) {
          device.getDevices().then(function (devices) {
            vm.devices = devices;
          });
        }else {
          vm.devices=[];
        }
      }


      function gotoDevice(key) {
        vm.isOpen = false;
        $state.go('device', {key: key});
      }

      function open() {
        vm.isOpen = true;
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frontend')
    .directive('myNavbar', myNavbar);

  /** @ngInject */
  function myNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(auth,$state) {
      var vm = this;
      vm.status= auth.getStatus();
      vm.logout = auth.logout;
      vm.login = login;

      function login(){
        $state.go('login')
      }
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .service('device', Device);

  /** @ngInject */
  function Device($http) {
    var vm = this;
    vm.getDevices = getDevices;
    vm.getDevice = getDevice;
    vm.updateDevice = updateDevice;
    vm.offDevice = offDevice;


    function getDevices() {
      return $http.get('/api/device').then(function (resp) {
        return resp.data;
      });
    }

    function getDevice(device_name) {
      return $http.get('/api/device/' + device_name).then(function (resp) {
        return resp.data
      });
    }

    function updateDevice(device) {
      return $http.post('/api/device/' + device.name, device).then(function(resp){
        return resp.data;
      });
    }

    function offDevice(device) {
      device.animation = null;
      return $http.post('/api/device/' + device.name, device).then(function(resp){
        return resp.data;
      });
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .service('auth', auth);

  /** @ngInject */
  function auth($http,$rootScope,$state) {
    var vm = this;
    vm.getForm = getForm;
    vm.login = login;
    vm.logout = logout;
    vm.updateStatus = updateStatus;
    vm.getStatus = getStatus;
    vm.status = {'loggedIn': false, 'user': null};

    updateStatus();

    function getForm() {
      return $http.get('/api/login').then(function (response) {
        return response.data.form;
      });
    }

    function login(form) {
      return $http.post('/api/login', form).then(function () {
        vm.updateStatus()
      });
    }

    function logout() {
      return $http.post('/api/logout').then(function () {
        vm.updateStatus();
        $state.go('home');
      });
    }

    function updateStatus() {
      return $http.get('/api/user').then(function (resp) {
        vm.status.user = resp.data;
        var loggedIn =vm.status.loggedIn;
        vm.status.loggedIn = resp.data.user_id != 0;
        if(loggedIn != vm.status.loggedIn) {
          $rootScope.sidenav.update();
        }
        return vm.status;
      });
    }

    function getStatus() {
      return vm.status;
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .service('animations', Animations);

  /** @ngInject */
  function Animations($http) {
    var vm = this;
    vm.getAnimations = getAnimations;
    vm.getAnimation = getAnimation;


    function getAnimations() {
      return $http.get('/api/animation', {cache: true}).then(function (resp) {
        return resp.data;
      });
    }

    function getAnimation(key) {
      return getAnimations().then(function (animations) {
        var found= null
        angular.forEach(animations, function (v) {
          if (v.name == key) {
            found= v;
          }
        });
        return found;
      });
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($rootScope, $scope, auth, device) {
    var vm = this;

    activate();
    function activate() {
      if ($rootScope.sidenav != undefined) {
        $rootScope.sidenav.open();
      }
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($log, $state, auth) {
    var vm = this;
    vm.form = undefined;
    vm.error = undefined;

    vm.login = login;

    activate();

    function activate() {
      auth.getForm().then(function (data) {
        vm.form = data
      });
    }

    function login() {
      return auth.login(vm.form).then(function () {
        vm.error = undefined;
        $state.go('home');
      }, function (data) {
        vm.error = data.data.error;
      });
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('DeviceController', DeviceController);

  /** @ngInject */
  function DeviceController($stateParams, device, animations ) {
    var vm = this;
    vm.device = undefined;
    vm.animations = undefined;
    vm.animation = undefined;
    vm.update = update;
    vm.reload = reload;
    vm.off = off;
    vm.updateAnimation = updateAnimation;

    activate();

    function activate() {
      vm.reload();
      animations.getAnimations().then(function (animations) {
        vm.animations = animations;
      });
    }

    function reload() {
      device.getDevice($stateParams.key).then(function (device) {
        vm.device = device;
          vm.animation = device.animation.name;
      });
    }

    function off() {
      if (vm.device != undefined) {
        device.offDevice(vm.device).then(function (device) {
          vm.device = device;
          vm.animation = null;
        });
      }
    }

    var update_id =0; // prevent from update to old data
    function update() {
      update_id++;
      var current_id =update_id;
      if (vm.device != undefined) {
        device.updateDevice(vm.device).then(function (device) {
          if(update_id==current_id) {
            vm.device = device;
            if (device.animation != null) {
              vm.animation = device.animation.name;
            }
          }
        });
      }
    }

    function updateAnimation() {
      if (vm.device != undefined) {
        animations.getAnimation(vm.animation).then(function(animation){
          vm.device.animation = animation;
          vm.update();
        });
      }
    }


  }


  angular
    .module('frontend')
    .filter('orderByValue', function () {
      function strcmp ( str1, str2 ) {
        // http://kevin.vanzonneveld.net
        // +   original by: Waldo Malqui Silva
        // +      input by: Steve Hilder
        // +   improved by: Kevin van Zonneveld (http://kevin.vanzonneveld.net)
        // +    revised by: gorthaur
        // *     example 1: strcmp( 'waldo', 'owald' );
        // *     returns 1: 1
        // *     example 2: strcmp( 'owald', 'waldo' );
        // *     returns 2: -1

        return ( ( str1 == str2 ) ? 0 : ( ( str1 > str2 ) ? 1 : -1 ) );
      }

      return function (obj) {
        if (obj == undefined) {
          return [];
        }
        var array = [];
        Object.keys(obj).forEach(function (key) {
          // inject key into each object so we can refer to it from the template
          obj[key].name = key;
          array.push(obj[key]);
        });
        // apply a custom sorting function
        array.sort(function (a, b) {
          return strcmp ( a.name, b.name );
        });
        return array;
      };
    });
})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $http, $interval) {
    $log.debug('runBlock end');

    function UpdateCsrf() {
      $http.get('/csrf.js', {cache: false}).then(function (resp) {
        $http.defaults.headers.post['X-CSRFToken'] = resp.data.csrftoken;
      });
    }

    $interval(UpdateCsrf, 60*5*1000); // each 5 min
    UpdateCsrf();
  }

})();

(function() {
  'use strict';

  angular
    .module('frontend')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .state('login', {
        url: "/login",
        templateUrl: "app/login/login.html",
        controller: 'LoginController',
        controllerAs: 'vm'
      })
      .state('device', {
        url: "/device/:key",
        templateUrl: "app/device/device.html",
        controller: 'DeviceController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/');
  }

})();


